
.data-container {
  width: 600px;
  height: 200px;
  margin: 30px auto;
  text-align: center;
}
.data-list{
  display: inline-block;
}
.data-pass{
  display: inline-block;
  width: 40px;
  padding-top: calc(50% - 10px);
}
.data-pass a {
  text-decoration: none;
  display: block;
  border: 1px solid #ccc;
  padding: 2px;
  margin: 2px;
  border-radius: 5px;
  color: blue;
  box-shadow: 2px 1px 5px #ccc;
}
.data-select {
  display: inline-block;
}
.multi-wrapper {
  border: 1px solid blue;
  width: 200px;
  height: 100%;
  padding: 10px 15px;
  text-align: left;
  vertical-align: top;
}
